<template>
    <section>
        <toolbar
            :buttonList="buttons"
            @callFunction="callFunction"
            placeholder="角色名称过滤"
        ></toolbar>
        <!--列表-->
        <el-table
            :data="roles"
            v-loading="listLoading"
            :row-class-name="$tableRowClassName"
            style="width: 100%"
        >
            <el-table-column type="index" width="150" align="center">
                <template slot-scope="scope">
                    <div
                        v-text="(page - 1) * pageSize + 1 + scope.$index"
                    ></div>
                </template>
            </el-table-column>
            <el-table-column
                prop="rolename"
                label="角色名称"
                min-width="1"
                align="center"
            >
            </el-table-column>
            <el-table-column
                label="操作"
                min-width="1"
                v-if="canEdit || canDelete"
            >
                <template slot-scope="scope">
                    <el-button
                        v-if="canEdit"
                        plain
                        type="warning"
                        size="small"
                        @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button
                    >
                    <el-button
                        v-if="canDelete"
                        plain
                        type="danger"
                        size="small"
                        @click="handleDel(scope.$index, scope.row)"
                        >删除</el-button
                    >
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                layout="total, prev, pager, next, jumper"
                @current-change="handleCurrentChange"
                :page-size="pageSize"
                :current-page="page"
                :total="total"
                style="float: right"
            >
            </el-pagination>
        </el-col>

        <!--编辑界面-->
        <el-dialog
            title="编辑"
            :visible.sync="editFormVisible"
            v-model="editFormVisible"
            :close-on-click-modal="false"
        >
            <el-form
                :model="editForm"
                label-width="120px"
                :rules="editFormRules"
                ref="editForm"
            >
                <el-col :span="12">
                    <el-form-item label="角色名称" prop="rolename">
                        <el-input
                            v-model="editForm.rolename"
                            auto-complete="off"
                            maxlength="20"
                        ></el-input>
                    </el-form-item>
                </el-col>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"
                    >取消</el-button
                >
                <el-button
                    type="primary"
                    @click.native="editSubmit"
                    :loading="editLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>
    </section>
</template>

<script>
import {
    getFrontRoleListPage,
    deleteFrontRole,
    editFrontRole,
    addFrontRole,
} from "../../api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
export default {
    components: { Toolbar },
    data() {
        return {
            buttons: [],
            filters: {
                name: "",
            },
            roles: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                rolename: [
                    {
                        required: true,
                        message: "请输入角色名称",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            isEdit: false,
        };
    },
    methods: {
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getRoleList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getFrontRoleListPage(paras).then((res) => {
                if (res.success) {
                    this.roles = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getRoleList(val);
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.ROLEID };
                    deleteFrontRole(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getRoleList(this.page);
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.isEdit = true;
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.editForm = {};
            this.isEdit = false;
            this.editFormVisible = true;
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    if (this.isEdit) {
                        editFrontRole(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "修改成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.getRoleList(this.page);
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }

                            this.editLoading = false;
                        });
                    } else {
                        addFrontRole(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "新增成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.getRoleList(this.page);
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }

                            this.editLoading = false;
                        });
                    }
                }
            });
        },
    },
    mounted() {
        this.getRoleList();

        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttons.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
</style>


